week6-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background-color: #5d9d29; /* Light blue background */
}

/* Task container styles */
.task-container {
  margin: 20px;
  padding: 20px;
  border: 10px solid #00b6ed; /* Black border */
  border-radius: 10px;
}

/* Button styles */
.toggle-button,
.user-type-button {
  background-color: #6b0992; /* Dark turquoise */
  color: #fff;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  margin-right: 10px;
  transition: background-color 0.3s ease;
}

.toggle-button:hover,
.user-type-button:hover {
  background-color: #e108b2; /* Light green on hover */
}

/* Component styles */
.component {
  margin: 20px;
  padding: 20px;
  background-color: #980000; /* Dark red background */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(193, 75, 28, 0.973);
}

/* Colored line */
.colored-line {
  border: 1px solid #7e8514; /* Dark blue line */
  width: 100%;
  margin: 40px auto;
}

/* Student details container */
.student-details-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 40px;
}

/* Student details card */
.student-details {
  margin: 20px;
  padding: 20px;
  border: 3px solid #c50629; /* Red border */
  border-radius: 10px;
  background-color: #68d743; /* Light yellow background */
  box-shadow: 0 4px 8px rgb(63, 4, 4);
  width: 220px;
}

/* Subjects list styles */
.subjects-list ul {
  padding-left: 20px;
}
